import type { Metadata } from 'next';
import './globals.css'; // path relative to src/app/layout.tsx
import '../lib/amplifyClient'; // path relative to src/app/layout.tsx
import AppShell from './AppShell';
import BottomNav from '../components/BottomNav';

export const metadata: Metadata = {
  title: 'NovelPedia',
  description: 'NovelPedia - Your Ultimate Destination for Web Novels',
  icons: {
    icon: '/favicon.ico', // âœ… relative to the `public/` folder
  },
};

export default function AppShell({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const hideNav = pathname?.startsWith('/login') || pathname?.startsWith('/signup');

  return (
    <div className="max-w-screen-sm mx-auto relative min-h-screen pb-24">
      {children}
      {!hideNav && <BottomNav />}
    </div>
  );
}
